import React from 'react';
import './Header.css';
import AnimatedLink from '../AnimatedLink/AnimatedLink';

function Header() {
    return (
        <header className="header">
            <div className="container">
                <nav className='navbar'>
                    <a data-aos="zoom-out-down" className="navbar-brand" href="/#">
                        LOGO
                    </a>
                    {/* <button className="navbar-toggler"></button> */}
                    <div className="navbar-collapse">
                        <div className="navbar-nav">
                            <div 
                            className="nav-item"
                            data-aos="zoom-out-down"
                            data-aos-delay="100">
                                <AnimatedLink href="/#" text="About" initialDelay={0}/>
                            </div>
                            <div 
                            className="nav-item"
                            data-aos="zoom-out-down"
                            data-aos-delay="200">
                                <AnimatedLink href="/#" text="Video" initialDelay={200}/>
                            </div>
                            <div 
                            className="nav-item"
                            data-aos="zoom-out-down"
                            data-aos-delay="300">
                                <AnimatedLink href="/#" text="Features" initialDelay={400}/>
                            </div>
                            <div 
                            className="nav-item"
                            data-aos="zoom-out-down"
                            data-aos-delay="400">
                                <AnimatedLink href="/#" text="Feedback" initialDelay={600}/>
                            </div>
                            <div 
                            className="nav-item"
                            data-aos="zoom-out-down"
                            data-aos-delay="500">
                                <AnimatedLink href="/#" text="FAQ" initialDelay={800}/>
                            </div>
                        </div>
                    </div>
                    <a 
                    href="/#" 
                    className="navbar-button" 
                    data-aos="zoom-out-down"
                    data-aos-delay="600"
                    >
                        <button className='btn'>Buy Now</button>
                    </a>
                </nav>
            </div>
        </header>
    )
}

export default Header;