import React, { useState, useEffect } from 'react';
import { useInView } from 'react-intersection-observer';
import './Home.css';
import AnimatedBlock from '../../components/AnimatedBlock/AnimatedBlock';
import Accordion from '../../components/AccordionItem/AccordionItem';

function Home() {
    const [scrollPosition, setScrollPosition] = useState(0);
    const [inViews, setInViews] = useState(false);
    const [isVisible, setIsVisible] = useState(true); // Отвечает за видимость блока
    const threshold = 10; // Значение порога для появления/исчезновения блока
    const { ref, inView } = useInView({
        triggerOnce: false,  // Чтобы анимация проигрывалась только один раз
        threshold: 0.2,      // 20% видимости
    });

    useEffect(() => {
        const handleScroll = () => {
          const rootFontSize = window.innerWidth / 100; // 1vw = 1% от ширины окна
          const sectionOneHeight = 62 * rootFontSize; // Высота первой секции в пикселях
          const sectionTwoStart = sectionOneHeight; // Начало второй секции
          const scrollPosition = window.scrollY;
    
          // Проверяем, находится ли текущая позиция скролла внутри второй секции
          if (scrollPosition >= sectionTwoStart + threshold * rootFontSize) {
            setIsVisible(false); // Прячем блок
          } else {
            setIsVisible(true); // Показываем блок
          }
        };
    
        window.addEventListener("scroll", handleScroll);
    
        // Убираем обработчик при размонтировании
        return () => window.removeEventListener("scroll", handleScroll);
      }, [threshold]);
    

    useEffect(() => {
        const handleScroll = () => {
            setScrollPosition(window.scrollY);
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    useEffect(() => {
        const handleScroll = () => {
            const section = document.querySelector('.recieve-section');
            const rect = section.getBoundingClientRect();
            if (rect.top < window.innerHeight && rect.bottom >= 0) {
                setInViews(true); // Секция в поле зрения
            } else {
                setInViews(false); // Секция вне поля зрения
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const getParallaxPosition = (baseSpeed) => {
        return scrollPosition * baseSpeed;
    };

    // const preloadImages = () => {
    //     const imagePaths = [
    //         '/levitation_gh/images/video-section/layers-bg-up-black.png',
    //         '/levitation_gh/images/video-section/layers-bg-up-white.png',
    //         '/levitation_gh/images/video-section/layers-bg-down-black.png',
    //         '/levitation_gh/images/video-section/layers-bg-down-white.png'
    //         // Добавьте сюда остальные пути
    //     ];
    //     imagePaths.forEach((src) => {
    //         const img = new Image();
    //         img.src = src;
    //     });
    // };

    // useEffect(() => {
    //     preloadImages();
    // }, []);

    return (
        <main className="main">
            <section className="section main-section" data-aos="fade">
                <div className="container">
                    <div className="main-section-content" style={{ transform: `translateY(${getParallaxPosition(0.6)}px)` }}>
                        <div data-aos="fade" data-aos-delay="100" className="block-the">
                            <img src="/levitation_gh/images/main-section/THE.png" alt="" />
                        </div>
                        <img data-aos="fade" data-aos-delay="400" className='character' src="/levitation_gh/images/main-section/levitate.png" alt="" />
                        <div data-aos="fade" className="block-levitation">
                            <img src="/levitation_gh/images/main-section/LEVITATION.png" alt="" />
                        </div>
                        <h3 data-aos="fade" data-aos-delay="200">Mushroom Trip Project</h3>
                        <p data-aos="fade" data-aos-delay="300">He world's first trip project where music and psychedelics merge to give you an unforgettable experience.</p>
                    </div>
                </div>
                {/* Параллакс-эффект для изображений ground с разными шагами */}
                <img
                    loading="eager"
                    className={`ground ground_0_0 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/background_1.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(1)}px)` }}
                />
                <img
                    loading="eager"
                    className={`ground ground_0_1 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/ground_0_1.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(0)}px)` }}
                />
                <img
                    loading="eager"
                    className={`ground ground_0_2 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/ground_0_2.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(0.2)}px)` }}
                />
                <img
                    loading="eager"
                    className={`ground ground_0_3 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/ground_0_3.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(0.4)}px)` }}
                />
                <img
                    loading="eager"
                    className={`ground ground_0_4 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/ground_0_4.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(0.6)}px)` }}
                />
                <img
                    loading="eager"
                    className={`ground ground_0_5 ${isVisible ? '' : 'hidden'}`}
                    src="/levitation_gh/images/main-section/ground_0_5.png"
                    alt=""
                    style={{ transform: `translateY(${getParallaxPosition(0.8)}px)` }}
                />
            </section>
            <section className="section recieve-section">
                <img className='mushroom1' src="/levitation_gh/images/recieve-section/mushrooms 1.png" alt="" />
                <img className='mushroom2' src="/levitation_gh/images/recieve-section/mushrooms 2.png" alt="" />
                <div className="container">
                    <div className="container-title">
                        <img
                            className={`title-object title-object-1 ${inViews ? 'animate-left' : ''}`}
                            src="/levitation_gh/images/recieve-section/mushroom.png"
                            alt=""
                        />
                        <div className="title" data-aos="fade">WHAT YOU GET?</div>
                        <div className="subtitle" data-aos="fade" data-aos-delay="100">for $50</div>
                        <img
                            className={`title-object title-object-2 ${inViews ? 'animate-right' : ''}`}
                            src="/levitation_gh/images/recieve-section/mushroom2.png"
                            alt=""
                        />
                    </div>
                    <div className="container-content">
                        <div className="block-content">
                            <img data-aos="zoom-in" data-aos-delay="200" src="/levitation_gh/images/recieve-section/mushroom-one.png" alt="" />
                            <div data-aos="fade" data-aos-delay="300" className="block-title">Exclusive access through the Telegram bot</div>
                            <div data-aos="fade" data-aos-delay="400" className="block-subtitle">Only for you, with no possibility of copying.</div>
                        </div>
                        <div className="block-content">
                            <img data-aos="zoom-in" data-aos-delay="300" src="/levitation_gh/images/recieve-section/mushroom-two.png" alt="" />
                            <div data-aos="fade" data-aos-delay="400" className="block-title">5-hour unique musical accompaniment</div>
                            <div data-aos="fade" data-aos-delay="500" className="block-subtitle">A specially crafted music set that guides you through all phases of the trip, enhancing every sensation.</div>
                        </div>
                        <div className="block-content">
                            <img data-aos="zoom-in" data-aos-delay="400" src="/levitation_gh/images/recieve-section/mushroom-three.png" alt="" />
                            <div data-aos="fade" data-aos-delay="500" className="block-title">Detailed instructions and advice on psilocybin mushrooms</div>
                            <div data-aos="fade" data-aos-delay="600" className="block-subtitle">From dosage to preparation and trip stages, supported by clinical research from institutes: Imperial College London, Johns Hopkins University, MAPS.</div>
                        </div>
                    </div>
                </div>
            </section>
            <section className="section video-section" ref={ref}>
                <div className={`layers-bg-up ${inView ? 'animate' : ''}`}>
                    <img
                        className="layers-bg-img layers-bg-up-white"
                        src="/levitation_gh/images/video-section/layers-bg-up.png"
                        alt="" />
                </div>
                <div className={`video-section-bg ${inView ? 'animate' : ''}`}></div>
                <div className="container">
                    <div data-aos="fade" data-aos-delay="1000" className="video-container">
                        <div className="container-title">
                            <img
                                className={`title-object title-object-1 ${inViews ? 'animate-left' : ''}`}
                                src="/levitation_gh/images/video-section/caption-graphic.svg"
                                alt=""
                            />
                            <div className="title" data-aos="fade">1 HOUR TRIAL</div>
                            <img
                                className={`title-object title-object-2 ${inViews ? 'animate-right' : ''}`}
                                src="/levitation_gh/images/video-section/caption-graphic-rotate.svg"
                                alt=""
                            />
                        </div>
                    </div>
                </div>
                <div className={`layers-bg-down ${inView ? 'animate' : ''}`}>
                    <img loading="eager" className="layers-bg-img layers-bg-down-white" src="/levitation_gh/images/video-section/layers-bg-down.png" alt="" />
                </div>
            </section>
            <section className='section product-section'>
                <div className="container">
                    <AnimatedBlock />
                </div>
            </section>
            <section className='section faq-section'>
                <div className="container">
                    <div className="section-title-block">
                        <div className="title">FAQ</div>
                        <div className="subtitle">We have tried and answered every important question of yours in advance.</div>
                    </div>
                    <Accordion />
                </div>
            </section>
        </main>
    );
}

export default Home;
